.missing {
  height: calc(100% - 60px);
  padding-bottom: 100px;
}

.missing_title {
  text-align: center;
  color: #6b6d05;
  padding-top: 60px;
  padding-bottom: 30px;
}

.missing_info {
  padding-bottom: 10px;
  position: relative;
  bottom: 70px;
  color: #6b6d05;
  overflow: scroll;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.missing_info > img {
  text-align: center;
  border: 0;
  border-radius: 10%;
  box-shadow: 0px 11px 35px 2px rgba(0, 0, 0, 0.14);
}

.found {
  height: calc(100% - 60px);
  padding-bottom: 100px;
}

.found_title {
  text-align: center;
  color: #6b6d05;
}

.found_info {
  padding-bottom: 50px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #6b6d05;
}

.found_info > img {
  text-align: center;
  border: 0;
  border-radius: 10%;
  box-shadow: 0px 11px 35px 2px rgba(0, 0, 0, 0.14);
}

